html {
  font-size: 10px;
}
body {
  position: relative;
  min-width: 320px;
  font-family: 'Source Sans Pro', 'Arial', sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.4;
  font-style: normal;
  color: #333333;
  background-color: #EDEDED;
}
body.lock {
  overflow: hidden;
}
main {
  position: relative;
  min-height: calc(100vh - 70px - 100px);
}
main > section {
  margin-bottom: 3rem;
}
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  border: none;
  outline: none;
}
img,
svg {
  display: inline-block;
  max-width: 100%;
  max-height: 100%;
}
a {
  color: #34AAF2;
  text-decoration: none;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
button {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.wrapper {
  width: 100%;
  max-width: 1070px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .wrapper {
    padding: 0 1.5rem;
  }
}
.phone {
  transition: all 0.3s ease-out;
}
.phone:hover,
.phone:focus {
  opacity: 0.7;
}
.phone:active {
  opacity: 0.4;
}
.title_page {
  padding-top: 51px;
  padding-bottom: 42px;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 5.2rem;
  line-height: 1.1;
  font-weight: 900;
  color: #00B956;
  text-align: center;
}
@media (max-width: 767px) {
  .title_page {
    padding: 2rem 0;
    font-size: 3rem;
  }
}
.no-js .accordion-list__content {
  display: block;
}
.no-js .accordion-list > .accordion-list__item:last-child {
  padding-bottom: 1px;
  margin-bottom: 15px;
}
.accordion-list > .accordion-list__item {
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}
.accordion-list > .accordion-list__item:not(:first-child) {
  margin-top: -1px;
}
.accordion-list > .accordion-list__item_active .accordion-list__title::after {
  transform: translate(-50%, -50%) rotate(-45deg) skew(5deg, 5deg);
}
.accordion-list_instruction .accordion-list__content {
  margin-left: 8rem;
}
.accordion-list__title {
  position: relative;
  padding: 2.8rem 1.5rem;
  padding-right: 5rem;
  font-size: 1.9rem;
  font-weight: 400;
  cursor: pointer;
}
.accordion-list__title_image {
  padding-left: 11rem;
}
.accordion-list__title_image img {
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
  max-width: 65px;
  max-height: 100%;
  width: auto;
  height: auto;
}
.accordion-list__title::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translate(-50%, -50%) rotate(135deg) skew(5deg, 5deg);
  display: block;
  width: 10px;
  height: 10px;
  border-top: 1px solid #666666;
  border-right: 1px solid #666666;
  transition: 0.3s ease-out;
}
.accordion-list__content {
  display: none;
  margin: 1.5rem;
  margin-top: 0;
}
@media (max-width: 767px) {
  .accordion-list_instruction .accordion-list__content {
    margin-left: 1.5rem;
  }
  .accordion-list > .accordion-list__item {
    margin-bottom: 1px;
    border-top: none;
    background-color: #ffffff;
  }
  .accordion-list > .accordion-list__item:last-child {
    border-bottom: none;
  }
  .accordion-list__title {
    padding: 2rem 1.5rem;
    padding-right: 5rem;
    font-size: 1.6rem;
  }
  .accordion-list__title_image {
    padding-left: 7rem;
  }
  .accordion-list__title_image img {
    left: 15px;
    max-width: 40px;
  }
  .accordion-list__title::after {
    right: 20px;
  }
}
.tabs-nav {
  position: relative;
  margin-bottom: 4rem;
}
.tabs-nav__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  background-color: #ffffff;
}
.tabs-nav__item {
  width: 50%;
  font-size: 2rem;
  border-bottom: 4px solid transparent;
  transition: all 0.3s ease-out;
}
.tabs-nav__item:nth-child(even) {
  border-left: 1px solid #d9d9d9;
}
.tabs-nav__item:nth-child(odd) {
  border-right: 1px solid #d9d9d9;
}
.tabs-nav__item_active {
  color: #00B956;
  border-color: #00B956;
}
.tabs-nav__item:hover,
.tabs-nav__item:focus {
  background-color: #f9f9f9;
}
.tabs-nav__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 3rem 1.5rem;
  font-weight: 600;
  color: inherit;
  cursor: pointer;
}
.tabs-nav__button:focus {
  outline: none;
}
.tabs-nav__button-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.8rem;
  height: 3rem;
  margin-right: 2.5rem;
}
.tabs-nav__button-icon svg {
  width: auto;
  height: auto;
}
.tabs-nav__button-text {
  display: block;
}
@media (max-width: 767px) {
  .tabs-nav__mobile-select {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
  }
  .tabs-nav__list::after {
    content: '';
    position: absolute;
    top: 2.5rem;
    right: 2.5rem;
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0.6rem 0.5rem 0 0.5rem;
    border-color: #333333 transparent transparent transparent;
    transition: all 0.3s ease-out;
  }
  .tabs-nav__list_open::after {
    transform: rotate(180deg);
  }
  .tabs-nav__item {
    width: 100%;
    border: 1px solid #D8D8D8;
  }
  .tabs-nav__item:not(:first-child) {
    display: none;
    border-top: none;
  }
  .tabs-nav__button {
    justify-content: flex-start;
    padding: 1.5rem 2.5rem;
    padding-right: 5rem;
    font-size: 1.5rem;
  }
  .tabs-nav__button-icon {
    width: 3rem;
    height: 2.5rem;
    margin-right: 1rem;
  }
}
@media (max-width: 375px) {
  .tabs-nav__button {
    padding: 1.5rem;
    padding-right: 4rem;
  }
}
.no-js .questions__item.accordion-list__item {
  margin-bottom: 1.5rem;
  background-color: #f9f9f9;
}
.questions__item {
  position: relative;
  padding: 1.5rem 3rem;
  padding-right: 6rem;
  font-size: 1.4rem;
}
.questions__item.accordion-list__item_active {
  margin-bottom: 1.5rem;
  background-color: #f9f9f9;
}
.questions__item.accordion-list__item_active .questions__close {
  opacity: 1;
}
.questions__item::before {
  content: '\2014';
  position: absolute;
  transform: translate(-150%, -1px);
  color: #34AAF2;
}
.questions__close {
  position: absolute;
  top: 5px;
  right: 16px;
  font-family: auto;
  font-size: 30px;
  line-height: 1;
  font-weight: 100;
  color: #999;
  opacity: 0;
  cursor: pointer;
  transition: all 0.3s ease-out;
}
.questions__close::after {
  content: '\00d7';
}
.questions__button {
  color: #34AAF2;
  border-bottom: 1px dashed transparent;
  transition: all 0.2s ease-out;
}
.questions__button:hover,
.questions__button:focus {
  border-color: inherit;
}
.questions__inner.accordion-list__content {
  margin: 0;
  margin-top: 1.8rem;
}
.questions__footer {
  display: flex;
  margin-top: 3rem;
  color: #999999;
}
.questions__footer p {
  display: inline-block;
  margin-right: 2.5rem;
}
.questions__footer .questions__success {
  display: none;
}
.questions__ask {
  display: flex;
}
.questions__answer {
  display: inline-block;
  margin-right: 1rem;
  color: #34AAF2;
  border-bottom: 1px solid transparent;
  transition: all 0.2s ease-out;
}
.questions__answer:hover,
.questions__answer:focus {
  border-color: inherit;
}
.questions__link span {
  margin-left: 5px;
  color: #333333;
}
@media (max-width: 767px) {
  .questions__item {
    padding-left: 2.5rem;
    padding-right: 4rem;
  }
  .questions__close {
    top: 7px;
    right: 6px;
  }
  .questions__footer {
    display: block;
    text-align: center;
  }
  .questions__footer p {
    margin-right: 0;
    margin-bottom: 5px;
  }
  .questions__ask {
    display: block;
  }
  .questions__answer {
    margin: 0 2.5rem;
  }
}
.page-header {
  min-height: 70px;
  padding: 20px 0;
  background-color: #ffffff;
}
.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page-header__nav {
  display: flex;
  align-items: center;
}
.page-header__logo {
  display: inline-block;
  width: auto;
  height: 3rem;
  transition: all 0.3s ease-out;
}
.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.7;
}
.page-header__logo:active {
  opacity: 0.4;
}
.page-header__logo img {
  width: auto;
  height: 100%;
  display: block;
}
.page-header__contacts {
  display: flex;
}
.page-header__phone {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 1.9rem;
  line-height: 1.2;
  font-weight: 500;
  color: #333333;
}
.page-header__phone-icon {
  display: none;
  width: 3.2rem;
  height: 3.2rem;
}
.page-header__phone-icon svg {
  display: inline-block;
  width: 100%;
  height: 100%;
}
@media (max-width: 767px) {
  .page-header {
    min-height: auto;
    padding: 15px 0;
  }
  .page-header__logo {
    height: 2.4rem;
  }
  .page-header__phone-icon {
    display: block;
  }
  .phone__text {
    display: none;
  }
}
.page-footer {
  display: flex;
  align-items: center;
  min-height: 100px;
  padding: 2rem 0;
}
.page-footer__copyright {
  max-width: 550px;
  font-size: 12px;
}
.no-js .tabs__item:not(:first-child) {
  display: block;
}
.no-js .tabs__item_instruction .tabs__header {
  display: block;
}
.tabs__content {
  padding: 3rem 4rem;
  background-color: #ffffff;
}
.tabs__header {
  padding: 2rem 0;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 2.4rem;
  font-weight: 400;
  text-align: center;
}
.tabs__item_instruction {
  display: none;
}
.tabs__item:not(:first-child) {
  display: none;
}
@media (max-width: 767px) {
  .tabs__content {
    padding: 0;
    background-color: transparent;
  }
  .tabs__header {
    padding: 0;
    font-size: 1.9rem;
  }
  .tabs__accordion {
    margin-top: 1.5rem;
  }
}
